import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
import utils from '../../library'
import Curriculum from '../Curriculum/Curriculum'
import Meta from '../Meta/Meta'
import MetaInput from '../MetaInput/MetaInput'
import MiniMetaLink from '../MetaLink/MiniMetaLink'
import MiniMetaList from '../MetaLink/MiniMetaList.js'
import MetaLink from '../MetaLink/MetaLink'
import Folder from '../Folder/Folder'


class Lips extends Component {
   state={
        user:"",
        account:"",
        avatar:"",
        vault:"intro",
        button_label:" PAGE BOX",
        meta:"",
        ico:"",
        folder:"",
        link:"",
        standard:"item",
        description:"",
        keywords:"",
        counter:"",
        loading:"0",
        flyer:"1",
        locked:"false"

    }

    async lockNow(){
        await utils.lockLips(this.props.net,getUrlVars()["cx"],getUrlVars()["id"])
        this.setState({locked:""+await utils.isPageLocked(this.props.net,getUrlVars()["id"])})
    }

     
    async setVault(_vault){   
     
        let _ico=""
            let b_lab=" PAGE BOX"
            let v=_vault   
            let fly=""+(parseInt(this.state.flyer)+1)
            if(_vault!="meta")
        try{  
            if(this.state.vault=="intro"){_ico=""; b_lab=" INTRO"; v="folder"}
            if(this.state.vault=="folder"){_ico=""; v="intro"; b_lab=" PAGE BOX" }
            }catch(e){}
        await this.setState({loading:"0",vault:v,ico:v+" "+_ico,button_label:b_lab,flyer:fly})
    }


    async setStandard(stn){
        await this.setState({standard:stn})

        document.getElementById(this.state.flyer).outerHTML=""
        let fly=""+(parseInt(this.state.flyer)+1)
        let d=document.createElement("div")
        d.setAttribute("id",fly)
        d.setAttribute("class","personalfeed_panel")
        document.getElementById("personal_feed_container").append(d)
        await this.setState({flyer:fly})

        let count=await this.loadAllPersonalEvents("default",utils.getUrlVars()["account"],this.state.vault,stn,await document.getElementById(this.state.flyer))
        //let load=parseInt(this.state.loading)+1
        this.setState({counter:count.length,loading:"0"})
    }


    async componentDidMount() {
        let _folder
        let _links
        let metadata
        let meta
        let linkout
        let t=this
       try{ 
           
          meta=await utils.pageMeta(getUrlVars()["cx"],getUrlVars()["id"])
           metadata=await utils.itemMetaJSON(meta[1])

        await utils.setBackground(metadata)
       document.getElementById("personal_name").textContent=cleanText(metadata["name"])
       let desc
       try{desc=metadata["description"]}catch(e){}
       let keywords
       try{keywords=metadata["keywords"]}catch(e){}
       let visible="intro"
       let settings=[]
       try{settings=metadata["settings"].split(",")
       
       for(let i=0;i<settings.length;i++){
           if(settings[i].indexOf("lips:")==0){
               let set=settings[i].substr(5)
            if(set.indexOf("visible:")==0)
            visible=set.substr(8)
           
           }
       }}catch(e){}
     
       await this.setState({name:metadata["name"],description:desc,keywords:keywords,vault:visible})
       linkout=metadata["external_url"]
       desc=desc.replaceAll("<br>","\n")
      
       _folder=metadata["folder"]
       _links=metadata["link"]
  
       //document.getElementById("personal_description").textContent=cleanText(desc.replaceAll("<br>","\n"))
        }catch(e){}
        
      this.setState({link:_links,folder:_folder,meta:meta[1].substr(12),linkout:linkout,locked:await utils.isPageLocked(t.props.net,getUrlVars()["id"])})
       
      await this.loadBlockchainData()
      try{
       document.getElementById("Avatar_face_container").innerHTML=""
       let avat = document.createElement('img')
       avat.setAttribute('src',utils.uri2url(metadata["image"]))
        avat.setAttribute('class',"avatar_banner")
        document.getElementById("Avatar_face_container").appendChild(avat)
      }catch(e){}
       utils.appendBlocky(getUrlVars()["cx"],40,document.getElementById("personalmenu"))
       // this.loadSections(getUrlVars()["account"],"all")
        
    }

    
async loadAllPersonalEvents(blck,who,vault,standard,element){
    let feed="y"
    let x=this
    await window.ethereum.enable().then(async function (accounts) { 
        try{
            //let mevents = new web3.eth.Contract(JSON.parse(REACT_APP_EMITTERABI),library.getVault(vault,standard))
            let mevents = new web3.eth.Contract(utils.abi("emitter"),utils.contract(x.props.net,standard,vault))
            let block=(blck=="default")?parseInt(await utils.getBlock())-100000:parseInt(blck)
            let eventz 
            
                let arr=[who]
                arr=who
                eventz = await mevents.getPastEvents('ncx',
                {filter : { "who":who} , fromBlock: block, toBlock: 'latest'
                } , async function(error, event){ 
                    feed=event
                    console.log("loadAllPersonalEvents")
                    console.log(event)

                    for(let i=event.length-1;i>=0;i--){
                   x.waiting(x,standard,event,i,element)
                    }
                })
            
            
        }catch(e){console.log("loadAllPersonalEvents "+e.toString())}
    })
    return feed
}

async waiting(x,standard,event,i,element){
    await utils.loadAndDisplayAsync(standard,event,i,element)
    let t=parseInt(x.state.loading)+1
    x.setState({loading:t})
}


async componentDidUpdate(){
    this.render()
}
    
    
    async loadBlockchainData() {
            let account
            let _user
            await window.ethereum.enable().then(async function (accounts) {
                _user=accounts[0]
                account=await utils.get721Owner(getUrlVars()["cx"],getUrlVars()["id"])
                let pageURI=utils.pageMeta()
            })
            this.setState({user:_user,avatar:account,account:account})  
        }
        MetaList = () => {
      
        let listItems
        if(this.state.folder) listItems = this.state.folder.split(",").map((addr) =>
          // Correct! Key should be specified inside the array.
          <MetaLink address={addr} key={Math.random()} keyx={Math.random()}/>
      
        );
        return (
          <>
            {listItems}
          </>
        );
    }
          
         keys2tags(){
             let keys=this.state.keywords
             keys="#"+keys
            keys.replaceAll(","," #")
            return keys
            } 
        

    render(){
  
        console.log(this.state.account,this.state.user)
        console.log("standard",this.state.standard)
        console.log("vault",this.state.vault)
        let has_item=false
        if(this.state.visual_item)has_item=true
        if(this.state.vox_item)has_item=true
        if(this.state.audio_item)has_item=true
        if(this.state.video_item)has_item=true
        let has_erc721=false
        if(this.state.visual_erc721)has_erc721=true
        if(this.state.vox_erc721)has_erc721=true
        if(this.state.audio_erc721)has_erc721=true
        if(this.state.video_erc721)has_erc721=true
        return ( 
            <>
            
       
            <div className="lips_left_col">
                {(this.state.vault!="meta")?
                    <>
                       
                        <div className="clear"></div>
                        {((this.state.vault=="intro")||(this.state.vault=="all"))?<div id="personal_description" className="personal_description">{this.state.description}</div>:<></>}

                        <div className="clear"></div>
                        {((this.state.vault=="folder")||(this.state.vault=="all"))?
                        <div>
                            <div className="lips_folder_label">Folder</div>
                            <div className="lips_folder">
                            <MiniMetaList links={this.state.link}  format="display"></MiniMetaList>
                            </div>
                        </div>
                        :<></>
                        }
                    </>
                    :
                    <MetaInput account={getUrlVars()["cx"]} id={getUrlVars()["id"]} lips="true" info={this.props.info} standard="nftpage"></MetaInput>
                }
            <div className="clear"></div>
            
            </div>
            <div className="cardpanel">
            <div id="Avatar_face_container" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => window.open('https://etherscan.com/address/'+getUrlVars()["account"])}></div>
            <div className="clear"></div>
            <div id="personal_name" className="Avatar_panel"></div>
            <div className="clear"></div>
            
            <>
                <div id="personalmenu" className="personalmenu darkborder">
                {(this.state.account.toLowerCase()==this.state.user.toLowerCase())?
                    <div className="personal_meta" onClick={() => this.setVault("meta")}></div>
                    :
                    <></>
                }
                 {(this.state.locked!="false")?<div id="lips_locked" className="personal_meta"></div>:<div id="lips_unlocked" className="personal_meta" onClick={() => this.lockNow()}></div>}
                    <div className="personal_meta" onClick={() => window.open("https://ipfs.io/ipfs/"+this.state.meta)}></div>
                
                </div>
                
                <div className="clear"></div>
            </>
           
           
           <div id="Avatar_panel" className="Avatar_blockie">
               
            
            
        </div>
        <div className="clear"/>
        {(this.state.vault!="all")?
        (this.state.folder)?
        <>
            
        <div id="page_intro" className="page_inventory dark darkblue" onClick={() => this.setVault("folder")}>{this.state.button_label}</div>
        </>
        :
        <></>
        
        :<></>
    }
        
        <MiniMetaLink address={"polygon:"+this.state.account}></MiniMetaLink>
        {(this.state.link)?
        <>
            <div className="links_label text white"></div>
            <MiniMetaList links={this.state.link}  format="mini"></MiniMetaList>
            </>
        :
        <></>
        }
        <img src="https://i.imgur.com/xYm6D98.png" id="twitter_sharebutton" onClick={() => window.open("https://twitter.com/intent/tweet?url="+encodeURIComponent(this.state.linkout)+"&text="+encodeURIComponent("CypherVault: "+this.state.name+" "+this.keys2tags()))}></img>
        
        </div>
       
        </>
        );
    }


}

function getAvatar(res){
    let avatar="";
    if(res.slice(-1)==="0")avatar="";
    if(res.slice(-1)==="1")avatar="";
    if(res.slice(-1)==="2")avatar="";
    if(res.slice(-1)==="3")avatar="";
    if(res.slice(-1)==="4")avatar="";
    if(res.slice(-1)==="5")avatar="";
    if(res.slice(-1)==="6")avatar="";
    if(res.slice(-1)==="7")avatar="";
    if(res.slice(-1)==="8")avatar="";
    if(res.slice(-1)==="9")avatar="";
    if(res.slice(-1)==="a")avatar="";
    if(res.slice(-1)==="b")avatar="";
    if(res.slice(-1)==="c")avatar="";
    if(res.slice(-1)==="d")avatar="";
    if(res.slice(-1)==="e")avatar="";
    if(res.slice(-1)==="f")avatar="";
    return avatar;
}

