async componentDidMount(){
  
   if(this.props.lips=="true"){
        this.loadMeta()
        this.setState({standard:"nftpage"})
        document.getElementById("Shop_meta_setmeta_button").style.opacity="1.0"
    }

    if(this.props.lips=="submit_card"){
        await this.setState({standard:this.props.standard,type:"card"})
        this.loadMeta()
        
        document.getElementById("Shop_meta_setmeta_button").style.opacity="1.0"
    }

    if(this.props.lips=="edit_collection"){
        await this.setState({standard:this.props.standard,type:"collection"})
        this.loadMeta()
        
        document.getElementById("Shop_meta_setmeta_button").style.opacity="1.0"
    }
    this.setState({standard:this.props.standard,type:this.props.type,amount:this.props.amount})
}



async loadMeta(){
  
        let addr=""
        let _dark
        let _light
        let _text
        let _cover="https://ipfs.io/ipfs/QmasZhThNG8eN5drZwpwxMUSUQnWYH3t4hZW6fwrF9jsHi"
        let _highres="https://ipfs.io/ipfs/QmasZhThNG8eN5drZwpwxMUSUQnWYH3t4hZW6fwrF9jsHi"
        let _filedata
        let _linkout
        let t=this
        await window.ethereum.enable().then(async function (accounts) {
            let metafound=false
            let mtdt
 
            if(t.props.lips=="true"){
                addr=t.props.account
                const metadata = new web3.eth.Contract(utils.abi("erc721"), addr) 
                mtdt=await metadata.methods.tokenURI(t.props.id).call()
                mtdt=mtdt.substr(12)
            }
   
            if(t.props.lips=="false"){
               
                addr=accounts[0]
                const metadata = new web3.eth.Contract(utils.abi("meta"), utils.contract(t.props.net,"meta")) 
                mtdt=await metadata.methods.meta(addr).call()
            }

            if(t.props.lips=="account"){
                addr=accounts[0]
                const metadata = new web3.eth.Contract(utils.abi("meta"), utils.contract(t.props.net,"meta")) 
                mtdt=await metadata.methods.meta(addr).call()
            }

            if(t.props.lips=="edit_collection"){
               // addr=accounts[0]
                const metadata = new web3.eth.Contract(utils.abi("card1155"), getUrlVars()["collection"]) 
                mtdt=await metadata.methods.uri(0).call()
               
            }
            if(mtdt)if(mtdt.length>=0)metafound=true
            console.log("MTDT>>>>LIPS> "+addr+" "+t.props.lips,mtdt)
            
            if((t.props.lips!="submit_card")&&(t.props.lips=="edit_collection"))if(!metafound){
            addr=accounts[0]
            const metadata = new web3.eth.Contract(utils.abi("meta"), utils.contract(t.props.net,"meta")) 
            mtdt=await metadata.methods.meta(addr).call()
            }
           // document.getElementById("Shop_twitter_pan").style.display='none'
            //document.getElementById("Shop_discord_pan").style.display='none'
      
            if(mtdt)if(mtdt.length>0){
               
                await fetch(formatURL(mtdt)).then(response => response.json()).then(
                response => {
                    console.log(JSON.stringify(response))
                  try{
                        try{document.getElementById("Shop_meta_setname").value=cleanText(response["name"])}catch(e){console.log(e)}
                        try{document.getElementById("Shop_meta_setdesc").value=resetBR(cleanText(changeBR(response["description"])))}catch(e){console.log(e)}
                        try{document.getElementById("Shop_meta_setsymbol").value=cleanText(response["symbol"])}catch(e){console.log(e)}
                        try{document.getElementById("Shop_meta_settags").value=cleanText(response["keywords"])}catch(e){console.log(e)}
                        try{document.getElementById("Shop_meta_setauthor").value=cleanText(response["author"])}catch(e){console.log(e)}
                        try{document.getElementById("Shop_meta_setinfo").value=resetBR(cleanText(changeBR(response["info"])))}catch(e){console.log(e)}
                        try{_cover=response["image"]; document.getElementById("cover_canvas").setAttribute("src",utils.uri2url(_cover))}catch(e){console.log(e)}
                        try{document.getElementById("Shop_meta_setcover").setAttribute("filedata",_cover)}catch(e){console.log(e)}
                        try{_highres=response["image_data"];if(_highres=="")_highres=_cover;if(_highres!="")document.getElementById("image_canvas").setAttribute("src",utils.uri2url(_highres))}catch(e){if(_highres=="")_highres=_cover;}
                        try{_filedata=response["file_data"];if(_filedata=="")_filedata=_cover}catch(e){console.log(e)}
                        try{document.getElementById("Shop_meta_setimage").setAttribute("filedata",_highres)}catch(e){console.log(e)}
                        try{document.getElementById("newmeta_setlinks").value=cleanText(response["link"])}catch(e){console.log(e)}
                        try{document.getElementById("newmeta_setcontent").value=cleanText(response["folder"])}catch(e){console.log(e)}
                        try{document.getElementById("newmeta_setsettings").value=cleanText(response["settings"])}catch(e){console.log(e)}
                        try{_linkout=response["linkout"]}catch(e){console.log(e)}
                        _dark=response["foreground_color"]
                        _light=response["background_color"]
                        _text=response["color"]
                        console.log("TEXT",_text)
                        utils.setBackground(response)
                        document.getElementById("personal_name").textContent=cleanText(response["name"])
                        document.getElementById("personal_description").textContent=cleanText(response["description"])
                    }catch(e){console.log(e)}
                    let back=response["image"]
                    if(back=="")back='https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/27592dd5-bdf2-4e90-9e17-852077de3dd0/d2aytcz-fd1a408a-541d-465d-9f8d-9c662b5ba6db.jpg/v1/fill/w_900,h_600,q_75,strp/fading_blue_grunge_by_dyingbeautystock.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..JSbyhtv1aQd2O8AzG2x1UTZLfyNdtwGNtNSgzKCDFTE'
                    
                    //document.getElementById("Shop_header_image").style.backgroundImage='url('+back+')'
        
                    
   
                })
            }

        })
        if(!_dark)_dark="#f5eaff"
        if(!_light)_light="#ffffff"
        if(!_text)_text="#bb7df8"
        utils.setBackgroundColors(_dark,_light,_text)
        //alert(utils.uri2url(_cover))
        //alert(utils.uri2url(_highres))
        if(!_highres)_highres=_cover
        //alert(_highres)
    await this.setState({light:_light,dark:_dark,text:_text,cover:utils.uri2url(_cover),image:utils.uri2url(_highres),filedata:_filedata,linkout:_linkout})
    document.getElementById("Shop_meta_setdark").setAttribute("col",_dark)
    document.getElementById("Shop_meta_setlight").setAttribute("col",_light)
    document.getElementById("Shop_meta_settext").setAttribute("col",_text)
}




