async checkID(){
    let ex=await utils.erc721exists(utils.getVault(this.state.type,this.state.standard),document.getElementById("Shop_meta_setid").value)
if(!ex)document.getElementById("Shop_meta_setid").style.background="white"
if(ex)document.getElementById("Shop_meta_setid").style.background="red"
if(!ex)this.setState({id:document.getElementById("Shop_meta_setid").value})
}

