        let file=image_data
        if(type=="card"){
            file=image_data+","+_back.split(",")[0]+",_"

            try{
                if(_back.split(",")[1]){file=file+","+_back.split(",")[1]}else{file=file+",_"}
            }catch(e){file=file+",_"}
        }
       

        if(type!="collection")
        if(document.getElementById("is_animated").checked){
            file=image_data+","+_back.split(",")[0]+","+utils.uri2url(checkFile("Shop_meta_setgifvideo"))
            try{file=file+","+_back.split(",")[1]
        }catch(e){file=file+",_"}
        }

        if(type=="collection")
        if(document.getElementById("is_animated").checked){
            let tfile=checkFile("Shop_meta_setgifvideo")
            if(tfile){
                file=image_data+","+utils.uri2url(tfile)  
            }else{
            file=image_data+",_"
            }
        }
        
