function check(ok,str){
    let _ok=ok
    if(str=="")if(ok)_ok=false
    if(!ok)_ok=false    
    return _ok
}

function checkPrice(ok,str){
    let _ok=ok

    if(parseFloat(str)==0)if(ok)_ok=false
    if(!ok)_ok=false 
    if(!_ok){
    document.getElementById("wait").textContent="uncomplete form!"
    document.getElementById("wait").style.color="red"
    }
    return _ok
}

function checkFileData(ok,element,state){
    let _ok=ok
    if(element=="cover")
    if(state=="")if(ok)ok=false
    if(element=="x")
    if(state=="")if(ok)ok=false
    if(!ok)_ok=false   
    if(!ok){
        document.getElementById("wait").textContent="uncomplete form!"
        document.getElementById("wait").style.color="red"
        console.log("FileData PROBLEM: ",element)
    }
    return _ok
}

function checkFile(element){
    let str=""
    try{
    let ele=document.getElementById(element)
    
    str=ele.getAttribute("filedata")
    if(str==""){
        ele.style.background="red"
        document.getElementById("wait").textContent="uncomplete form!"
        document.getElementById("wait").style.color="red"
        console.log("PROBLEM ",element)
    }
}catch(e){console.log(e)}
return str
}



        function cleanTags(element){
            let ele=document.getElementById(element)
            let str=ele.value
            if(str==""){
                ele.style.background="red"
                document.getElementById("wait").textContent="uncomplete form!"
                document.getElementById("wait").style.color="red"
            }else{
                ele.style.background="white"
                ele.style.color="black"
            if(str.indexOf(">")>=0)ele.style.background="orange"
            if(str.indexOf("<")>=0)ele.style.background="orange"
        
                str=str.replaceAll("<","")
                str=str.replaceAll(">","")
                str=str.replaceAll(", ",",")
                str=str.replaceAll(" ,",",")
            }
            ele.value=str
            return str
        }


function cleanPrice(element){
            let ele=document.getElementById(element)
            let t=ele.value
            if(t=="")t=0
            let str=parseFloat(t)
            if(str==0){
                ele.style.background="red"
            }else{
                ele.style.background="white"
                ele.style.color="black"
            }
            ele.value=str
            return str
        }

function clean(element){
    let ele=document.getElementById(element)
    let str=ele.value
    if(str==""){
        ele.style.background="red"
    }else{
        ele.style.background="white"
        ele.style.color="black"
    if(str.indexOf(">")>=0)ele.style.background="orange"
    if(str.indexOf("<")>=0)ele.style.background="orange"

        str=str.replaceAll("<","")
        str=str.replaceAll(">","")
    }
    ele.value=str
    return str
}

function cleanLink(element){
    let ele=document.getElementById(element)
    let str=""
    try{
        str=ele.value
    
    ele.style.background="white"
    ele.style.color="black"
    if(str.indexOf(">")>=0)ele.style.background="orange"
    if(str.indexOf("<")>=0)ele.style.background="orange"

        str=str.replaceAll("<","")
        str=str.replaceAll(">","")
    
    ele.value=str
    }catch(e){console.log(e)}
    return str
}

function cleanText(str){
    let _str=str
    _str=_str.replaceAll("\n","")
    _str=_str.replaceAll("\r","")
    return _str.replace(/[<>"]/gi, '');
}

function cleanHTML(str){
    let _str=str
    return _str.replace(/[<>"]/gi, '');
}

function changeBR(str){
    let _str=str
    _str=_str.replaceAll("<br>",".xxx.")
    return _str
}

function resetBR(str){
    let _str=str
    _str=_str.replaceAll(".xxx.","<br>")
    return _str
}


function formatURL(url){
    let t=url
    if(t.indexOf("ipfs://ipfs/")==0)t=t.substr(12)
    return("https://gateway.ipfs.io/ipfs/"+t);
  }

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}


        
