import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
import utils from '../../library'


class CollectionLinkMini extends Component {
    constructor(props){
        super()
   this.state={
        metalink:"",
        image:"https://i.imgur.com/TaFS2Rj.png",
        label:"",
        link_type:"collection",
        id:"0"
    }
}
 
    componentDidMount() {
       
    }
      
    async setLabel() {
        try{
            let meta
            meta=await utils.findMeta(this.props.address,0)
            
            if(meta[0]=="account")this.setState({link_type:"account"})
            console.log("SET LABEL ",meta[1])
            let itemmeta=await utils.itemMetaJSON(meta[1])
            console.log("NAME ",itemmeta["name"])
        let labe=itemmeta["name"]
        let ima=itemmeta["image"]
        document.getElementById("image"+this.props.address+"_"+this.props.keyx).setAttribute("src",utils.uri2url(ima))
        
        let dark=itemmeta["foreground_color"]
        let text=itemmeta["color"]
        try{
            let acc="account"
        let account_type=itemmeta["type"]
        console.log("FINAL METALINK META",itemmeta)
        if(account_type=="wallet")acc="account"
        if(account_type=="collection")acc="collection"
        if(account_type=="card")acc="cx"
            this.setState({link_type:acc})}catch(e){console.log(e)}
        document.getElementById(this.props.address+"_"+this.props.keyx).style.background=dark
        document.getElementById(this.props.address+"_"+this.props.keyx).style.color=text
        document.getElementById(this.props.address+"_"+this.props.keyx).textContent=cleanText(labe)
        this.setState({label:labe})
        }catch(e){
            try{
        this.setState({label:this.props.address})
        document.getElementById(this.props.address+"_"+this.props.keyx).textContent=cleanText(this.props.address)
        document.getElementById("image"+this.props.address+"_"+this.props.keyx).setAttribute("src","https://i.imgur.com/TaFS2Rj.png")
        }catch(e){console.log(e)}
    }
    }

  

    render(){
    
if(this.props.address&&(this.state.label=="")){
    this.setLabel()
}
        return ( 
            <div className="metalinkmini dark darkblue" onClick={() => window.open("/cyphervault/?"+this.state.link_type+"="+this.props.address+"&id="+this.state.id)}>
                <div className="metalink_image_container dark darkblue">
                    <img className="metalinkmini_image" id={"image"+this.props.address+"_"+this.props.keyx} src=""></img>
                </div>
                <div className="metalink_label_container dark darkblue" id={this.props.address+"_"+this.props.keyx}>
                
                </div>
            </div>
        );
    }


}

function cleanText(str){
    return str.replace(/[<>"]/gi, '');
}


function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

// <PersonalFeed update={this.props.update} vault={this.state.vault} avatar={this.state.avatar}></PersonalFeed>
           

const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default CollectionLinkMini