import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
import utils from '../../library'
import MiniMetaLink from '../MetaLink/MiniMetaLink'
import MetaLink from '../MetaLink/MetaLink'
import MiniDisplay from '../MetaLink/MiniDisplay'


class MiniMetaList extends Component {
   state={}
      
 
    MiniMetaList = () => {
        let listItems
        if(this.props.links) listItems = this.props.links.split(",").map((addr) =>
          // Correct! Key should be specified inside the array.
          (this.props.format=="mini")?<MiniMetaLink address={addr} key={Math.random()} keyx={Math.random()}/>:(this.props.format=="big")?<MetaLink address={addr} key={Math.random()} keyx={Math.random()}/>:<MiniDisplay address={addr} key={Math.random()} keyx={Math.random()}/>
      
        );
        return (
          <>
            {listItems}
          </>
        );
      }

     
   
   render(){
   
    return ( 
    <>{this.MiniMetaList()}</>
    );
}


}

function cleanText(str){
return str.replace(/[<>"]/gi, '');
}

function getUrlVars() {
var vars = {};
var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
    vars[key] = value;
});
return vars;
}

// <PersonalFeed update={this.props.update} vault={this.state.vault} avatar={this.state.avatar}></PersonalFeed>
       

const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default MiniMetaList