import React, { Component } from 'react'
import './css.css';
import utils from '../../library'

class How extends Component {
   
    render(){
        utils.setHomeColors()
        console.log(this.props.update) 
        return ( 
            <div>
                <div id="bows_flyer" className="text white">
                    <div id="bows_mainlabel"> HOW TO USE</div>
                    <div id="bows_sublabel">Introduction to Cyphervault</div>
                    <div className="bows_panel dark darkblue text white">
                        <div className="instructions_title light lightblue text black"> Collaborative Cards Decks</div>
                        <br></br>
                        CypherVault is in Beta version.
                        <br></br><br></br>
                        We are now experimenting its first operations on Ethereum L2 chain Polygon (MATIC).
                        <br></br><br></br>
                        Functionalities are temporary limited to CypherCards minting.
                        <br></br><br></br>
                        CypherCards are a collaborative/promotional initiative for the creation of a collection of artistic card decks.
                        <br></br><br></br>
                        Anyone can join a card collection or launch/curate his own deck and allow third party artworks to be submitted to his deck.
                        <br></br><br></br>
                        If interested join our Discord channel https://discord.gg/uSkY7Hc5YD
                       
                    </div>
               
                <br></br><br></br>
                <div className="bows_panel dark darkblue text white">
                        <div className="instructions_title light lightblue text black"> HOW TO CONNECT TO MATIC CHAIN</div>
                        <br></br>
                        In order to mint you will need to be able to connect to both Polygon (MATIC) main network and also to its test network (where you mint test NFT to ensure everything is good, then you move to main network and officially mint there)
                        <br></br><br></br>
                        You will need your Metamask extension in your browser, then please follow these instructions and enable your metamask to access MATIC. (See both tabs, MATIC-MAINNET and MUMBAI-TESTNET, and enable Metamask for both networks)
                        <br></br><br></br>
                        https://docs.matic.network/docs/develop/metamask/config-matic/
                        <br></br><br></br>
                        Once you are done you need some test MATIC tokens, you will use them on testnetwork (MUMBAI) to test the minting of your new NFT and ensure they work and appear properly, before moving to MATIC-MAINNET.
                        <br></br><br></br>
                        Pull test tokens here: 
                        <br></br><br></br>
                        https://faucet.matic.network/
                        <br></br><br></br>
                        <br></br><br></br>
                        Find more upcoming features in the Features Page!
                    </div>
                    <br></br><br></br>
                    <div className="bows_panel dark darkblue text white">
                        <div className="instructions_title light lightblue text black"> SUBMIT A CARD TO A DECK</div>
                        <br></br>
                        Reach the page of your favorite collection.
                        <br></br><br></br>
                        Read the INSTRUCTIONS for the specific collection.
                        <br></br><br></br>
                        Click ADD CARD and compile the form in order to submit your card.
                        <br></br><br></br>
                        Wait the specific Collection Curator to accept your card.
                        <br></br><br></br>
                        Cards go automatically to the autonomous CypherShop contract and are sold at incremental price by the contract itself. 
                        <br></br><br></br>
                        CypherShop gives away the cards for MATIC tokens.
                        <br></br><br></br>
                        1/3 of the income goes to the Collective Vault.
                        <br></br><br></br>
                        1/3 to the Curator of the specific colllection the card belongs to.
                        <br></br><br></br>
                        1/3 goes to the artist creator of the card.
                        <br></br><br></br>
                        
                    </div>
                    </div>
            </div>
        );
    }

}

export default How