setCookie: function(cname, cvalue,id) {
    let s=library.getCookie(cname)
    let arr=s.split(",")
    let arr2=[]
    let t=false
    for(let i=0;i<arr.length;i++){
        if(arr[i]==cvalue+"_"+id){
            t=true
        }else{
            arr2.push(arr[i])
        }
    }
    if(!t){
        arr2.push(cvalue+"_"+id)
    }
    document.cookie = cname + "=" + arr2.toString() + "; path=/"
    
},

