import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
import utils from '../../library'


class MetaLink extends Component {
    constructor(props){
        super()
   this.state={
        link:"",
        image:"https://i.imgur.com/TaFS2Rj.png",
        label:"",
        address:""
    }
}
 
    componentDidMount() {
      //  if(this.props.address&&(this.state.label=="")){
            this.setLabel()
      //  }
    }

    componentDidUpdate() {
        if(this.props.address!="polygon:"+this.state.address)
              this.setLabel()
    
      }
      
    async setLabel() {
        let metadata
        let text=""
        let dark=""
        let ima="https://i.imgur.com/TaFS2Rj.png"
        try{
            let address=this.props.address
          
            if(address.indexOf("polygon:")==0){
                
                address=address.substr(8)
                let id=0
                if(address.length>42){
                    id=address.substr(43)
                    address=address.substr(0,42)
                }
                console.log("MINIMETALINK "+address,id)
              
                let t=await utils.findMeta(address,id)
                
                console.log("FOUND "+address,t)

                if(t[1]!="ipfs://ipfs/")
                try{metadata=await utils.itemMetaJSON(t[1])}catch(e){console.log(e)}
                console.log("MINIMETALINK METADATA:"+address,metadata)
                
                if(t[0]=="account") this.setState({link:"account="+address,address:address})
                if(t[0]!="account")if(metadata["standard"]=="nftpage"){
                    this.setState({link:"lips=true&cx="+address+"&id="+id})
                }else{
                    this.setState({link:"cx="+address+"&id="+id,address:address})
                }
            }

            if(address.indexOf("meta:")==0){
            metadata=await utils.itemMetaJSON(address.substr(5))
            this.setState({link:"meta="+address.substr(5),address:address})
            }
        
           let labe=address
           if(metadata){
            labe=metadata["name"]
        
            ima=metadata["image"]
            let dark=metadata["foreground_color"]
            if(dark=="")dark=metadata["dark"]
            let text=metadata["color"]
           
          
           }
           document.getElementById(this.props.address+"_"+this.props.keyx).style.background=dark
           document.getElementById(this.props.address+"_"+this.props.keyx).style.color=text
           document.getElementById(this.props.address+"_"+this.props.keyx).textContent=cleanText(labe)
           document.getElementById("image"+this.props.address+"_"+this.props.keyx).setAttribute("src",utils.uri2url(ima))
  
           this.setState({label:labe})
            }catch(e){
                console.log(e)
           // document.getElementById(this.props.address+"_"+this.props.keyx).textContent=cleanText(this.props.address)
            //document.getElementById("image"+this.props.address+"_"+this.props.keyx).setAttribute("src","https://i.imgur.com/TaFS2Rj.png")
            }
    }

  

    render(){
        //alert(this.props.address)

        return ( 
            <div className="minimetalink dark darkblue" onClick={() => window.open("/cyphervault/?"+this.state.link)}>
                <div className="minimetalink_image_container dark darkblue">
                    <img className="minimetalink_image" id={"image"+this.props.address+"_"+this.props.keyx} src=""></img>
                </div>
                <div className="minimetalink_label_container dark darkblue" id={this.props.address+"_"+this.props.keyx}>
                
                </div>
                
            </div>
        );
    }


}

function cleanText(str){
    return str.replace(/[<>"]/gi, '');
}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

// <PersonalFeed update={this.props.update} vault={this.state.vault} avatar={this.state.avatar}></PersonalFeed>
           

const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default MetaLink