import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
import utils from '../../library'
import MetaLink from '../MetaLink/MetaLink'



class Suggestions extends Component {
    constructor(props) {
        super(props)
   this.state={
found:0,
loaded:0,
accepted_cards:""
   }
}
componentDidMount(){
    this.setState({accepted_cards:this.props.accepted})
    this.subscriptions()
}
  async subscriptions(){
   this.MetaList () 
    //console.log("SUBSCRIPTION 1",await utils.nextSubscription(getUrlVars()["collection"],1))
}    

async setLabel(address,i) {
    console.log("IPFS ADDRESS: ",address)
    try{
    let meta=await  utils.itemMetaJSON(utils.uri2url(address))
    console.log(meta)
    let labe=meta["name"]
    let ima=meta["image"]
    
    let dark=meta["foreground_color"]
    let text=meta["color"]
   let tempDiv=document.createElement("div")
   tempDiv.setAttribute("id","submission_"+i)
   tempDiv.setAttribute("class","submission_container")
   let tempImg=document.createElement("img")
   tempImg.onclick=() => this.seeMeta(address)
   tempImg.setAttribute("id","submission_img_"+i)
   tempImg.setAttribute("class","submission_image")
   tempImg.setAttribute("src",utils.uri2url(ima))
   tempDiv.append(tempImg)
   document.getElementById("suggestions_panel").append(tempDiv)
   let tempButton=document.createElement("input")
   tempButton.setAttribute("id","submission_button_"+i)
   tempButton.setAttribute("class","submission_accept_button")
   tempButton.setAttribute("type","button")
   tempButton.setAttribute("value","ACCEPT")
   tempButton.onclick=() => acceptCard(meta["standard"],i)
   let temp=this.props.accepted.split(",")
   console.log("accepted: ",temp)
   let f=false
   for(let x=0;x<temp.length;x++)if(labe==temp[x])f=true
   if(!f)tempDiv.append(tempButton)
   this.setState({loaded:this.state.loaded+1})

}catch(e){}
}

seeMeta(ipfs){
  window.open("/cyphervault/?meta=true&cx="+ipfs)
}



async MetaList () {

    let y=1;
    let waiting=[]
    for(let i=0;i<y;i++){
        let x
    try{x=await utils.nextSubscription(this.props.standard,getUrlVars()["collection"],i)
//console.log("LENGTH ",x[0].length)

console.log("X"+i,x)
if(x){
if(this.props.standard=="item"){
    if(x[0].length==42){y++;this.setState({found:this.state.found+1})}
    if(x)if(x[0].length==42)this.setLabel(x[4],i)
}
if(this.props.standard=="erc1155"){
    if(x[0].length==42){y++;this.setState({found:this.state.found+1})}
    if(x)if(x[0].length==42)this.setLabel(x[4],i)
}
}

}catch(e){}
    }
 
  }


   render(){

    return ( 
        <>
        {(this.state.loaded!=this.state.found)?<div className="suggestion_label">loaded {this.state.loaded} of {this.state.found} please wait...</div>:<div className="suggestion_label">SUBMISSIONS [ {this.state.found} ]</div>}
        <div id="suggestions_panel"></div>
        </>
        );
    }
        
}
function  acceptCard(standard,i){
    let t=this
    window.ethereum.enable().then(async function (accounts) {
        document.getElementById("submission_button_"+i).style.display="none"
        let ext=getUrlVars()["collection"]
        if(standard=="item"){
            const coll2ext= new web3.eth.Contract(utils.abi("ext2coll"),getUrlVars()["collection"]) 
            ext = await coll2ext.methods.extension().call()
        }
            let extC
            if(standard=="item")extC= new web3.eth.Contract(utils.abi("cardextension"),ext ) 
            if(standard=="erc1155")extC= new web3.eth.Contract(utils.abi("card1155"),ext ) 
            
            extC.methods.mintSetPrice(""+i,"","","0","").send({from: accounts[0] , gas: 2000000 })

    })
}




function cleanText(str){
    return str.replace(/[<>"]/gi, '');
}

    const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
    function getUrlVars() {
        var vars = {};
        var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
            vars[key] = value;
        });
        return vars;
    }
export default Suggestions