import React, { Component } from 'react'
import './css.css';
import utils from '../../library'

class Splash extends Component {
   
    render(){
        utils.setHomeColors()
        console.log(this.props.update) 
        return ( 
        <div>

        <div id="splash_flyer"><div id="splash_mainlabel">CypherVault</div>
            <img id="splash_imageMain"  alt="NFT"  src="https://i.imgur.com/gtMhrWq.png"  onClick={() => this.props.update("Features")}></img>
            <br></br>D.D.I.<br></br><br></br>
            <div className="splash_button" onClick={() => this.props.update("How")}> HOW TO USE</div>
            <div className="splash_button" onClick={() => this.props.update("Features")}> SEE FEATURES</div>
            {(0==1)?<div className="splash_button" onClick={() => this.props.update("Galleries")}> GALLERIES</div>:<></>}
            {(0==1)?<div className="splash_button" onClick={() => this.props.update("Pioneers")}> PIONEERS</div>:<></>}
            <div className="splash_button" onClick={() => this.props.update("Cards")}> CYPHERCARDS</div>
            {(0==1)?<div className="splash_button" onClick={() => this.props.update("Cyphers")}> THE CYPHERS</div>:<></>}
            {(0==1)?<div className="splash_button" onClick={() => this.props.update("Ufactory")}> DIRECTORY</div>:<></>}
            <div id="splash_sublabel">New Media Vault, Autonomous, Decentralized, Community Ruled</div>
        </div>
      
        </div>
        );
    }


}




export default Splash