import React, { Component } from 'react'
import Lipshead from '../Lipshead/Lipshead';
import Pad from '../Pad/Pad';
import Info from '../Info/Info';
import './css.css';
import utils from './../../library'
import Web3 from 'web3';


class Home extends Component { 
    constructor(props) {
      super(props)
      this.state = { 
      info:"" ,
      back:"",
      panel: 'Splash' ,
      token : '0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD',
      id:'0',
      net:"1"
      }

    }

    async componentDidMount(){
       let net=1
       let t=this
      await window.ethereum.enable().then(async function (accounts) { 
         window.ethereum.on('networkChanged',async function(netId){
         net=netId
                 if(netId=="3"){
                  document.getElementById("ropsten_message").textContent="WORKS ON ROPSTEN"
                  document.getElementById("ropsten_message").style.color="#bb7df8"
                 }else{
                  document.getElementById("ropsten_message").textContent="WRONG NETWORK > GO TO ROPSTEN"
                  document.getElementById("ropsten_message").style.color="red"
             }
             window.location.reload(true)
             if(t.state.net!=net)await t.setState({net:net})
          });
    await web3.eth.net.getId().then(async netId => {

      net=netId
     if(netId=="3")document.getElementById("ropsten_message").textContent="WORKING ON ROPSTEN"
     if(netId=="137")document.getElementById("ropsten_message").textContent="WORKING ON MATIC"
     if(netId=="80001")document.getElementById("ropsten_message").textContent="WORKING ON MUMBAI"
     if(netId=="80001")document.getElementById("ropsten_message").style.color="orange"
     if(netId!="3")if(netId!="137")if(netId!="80001")document.getElementById("ropsten_message").textContent="WRONG NETWORK > GO TO MATIC"
     if(netId!="3")if(netId!="137")if(netId!="80001")document.getElementById("ropsten_message").style.color="red"
     if(t.state.net!=net)await t.setState({net:net})
}) })

    }

    update(panelx,tokenx){
        this.setState({panel:panelx,token:tokenx})
        this.closeInfo("")
        if(panelx!="Avatar")window.history.pushState('new_panel', panelx, "/");
        console.log("changed page!",panelx);
     }

   info(field,back){
      this.setState({info:field,back:back})
document.getElementById("info_table").style.display="block"
document.getElementById("superpad").style.display="none"
   }

   closeInfo(field){
      this.setState({info:""})
      document.getElementById("info_table").style.display="none"
      document.getElementById("superpad").style.display="block"
   }
   

    render(){ 
    
        
       let panel=this.state.panel
       let token=this.state.token
       let id=this.state.id
       let meta=false
       if(utils.getUrlVars()["cx"]){
          panel="create"
          token=utils.getUrlVars()["cx"]
          id=0
          if(utils.getUrlVars()["id"]){
            id=utils.getUrlVars()["id"]
         }
         }
        
       if(utils.getUrlVars()["account"]){
          panel="Avatar"
          token=utils.getUrlVars()["account"]
         }

         if(utils.getUrlVars()["collection"]){
            panel="CardCollection"
            token=utils.getUrlVars()["collection"]
           }

         if(utils.getUrlVars()["lips"]){
            panel="Lips"
           }

           if(utils.getUrlVars()["meta"]){
            panel="create"
            meta=true
           }

         if(utils.getUrlVars()["tag"]){
            panel="Feed"
            token=utils.getUrlVars()["tag"]
           }

          let to=utils.getUrlVars()["to"]
        
        return (
        <div id="home" className="dark darkblue">
           <Lipshead update={this.update.bind(this)} net={this.state.net}></Lipshead>
           <Pad panel={panel} info={this.info.bind(this)} meta={meta} token={token} id={id} to={to} net={this.state.net} update={this.update.bind(this)}/>
           <Info closeInfo={this.closeInfo.bind(this)} info={this.state.info}></Info>
        </div>);
    }
}

const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default Home