import React, { Component } from 'react'
import './css.css';
import utils from '../../library'
import MiniMetaList from '../MetaLink/MiniMetaList';
import Web3 from 'web3';


class Inventory extends Component {
    state={
        user:"",
        vault:"intro",
        meta:"",
        ico:"",
        standard:"erc721",
        counter:"",
        loading:"0",
        flyer:"1",
        owned:""
    }

    componentDidMount(){
        this.setVault()
    }

    async setVault(){  
        let vault=document.getElementById("personal_view").value
        await this.setState({vault:vault})    
       
        let fly
            try{document.getElementById(this.state.flyer).outerHTML=""}catch(e){}
            fly=""+(parseInt(this.state.flyer)+1)
            let d=document.createElement("div")
            d.setAttribute("id",fly)
            d.setAttribute("class","inventory_content_panel")
            try{document.getElementById("inventory_feed_container").append(d)}catch(e){}

            let tfly=await document.getElementById(fly)
            let count
            if(vault=="visual")
            count=await this.loadAllPersonalEvents("default",utils.getUrlVars()["account"],"visual","erc721",tfly)
            if(vault=="vox")
            count=await this.loadAllPersonalEvents("default",utils.getUrlVars()["account"],"vox","erc721",tfly)
            if(vault=="audio")
            count=await this.loadAllPersonalEvents("default",utils.getUrlVars()["account"],"audio","erc721",tfly)
            if(vault=="video")
            count=await this.loadAllPersonalEvents("default",utils.getUrlVars()["account"],"video","erc721",tfly)
            if(vault=="lips")
            count=await this.loadAllPersonalEvents("default",utils.getUrlVars()["account"],"page","nftpage",tfly)
            if(vault=="card")if((this.props.net==137)||(this.props.net==80001))
            count=await this.loadMintedCollectionsMatic(utils.getUrlVars()["account"],tfly)
            if(vault=="card")if((this.props.net==1)||(this.props.net==3))
            count=await this.loadMintedCollections("default",utils.getUrlVars()["account"],"card","erc1155",tfly)
      
            // count+=parseInt(await this.loadAllPersonalEvents("default",utils.getUrlVars()["account"],"vox","erc721",tfly))

       this.setState({counter:count.length,loading:"0",flyer:fly})
    }

   
    async loadAllPersonalEvents(blck,who,vault,standard,element){

        let feed="y"
        let x=this
        let tst=standard
        if(tst=="interoperable")tst="item"
        let token=utils.contract("3",tst,vault)
     
        await window.ethereum.enable().then(async function (accounts) { 
            try{
                //let mevents = new web3.eth.Contract(JSON.parse(REACT_APP_EMITTERABI),library.getVault(vault,standard))
                let mevents = new web3.eth.Contract(utils.abi(standard),token)
                let block=(blck=="default")?parseInt(await utils.getBlock())-500000:parseInt(blck)
                let eventz 
                
                    let arr=[who]
                    arr=who
                    if((standard=="erc721")||(standard=="nftpage"))
                    eventz = await mevents.getPastEvents('Transfer',
                    {filter : { "to":who} , fromBlock: block, toBlock: 'latest'
                    } , async function(error, event){ 
                        feed=event
                        console.log("loadAllPersonalEvents")
                        console.log(event)
    
                        for(let i=event.length-1;i>=0;i--){
                       x.waiting(x,standard,event,i,element,token)
                        }
                    })

                    if(tst=="item")
                    eventz = await mevents.getPastEvents('Transfer',
                    {filter : { "to":who} , fromBlock: block, toBlock: 'latest'
                    } , async function(error, event){ 
                        feed=event
                        console.log("load All Item Transfers")
                        console.log(token,event)
    
                        for(let i=event.length-1;i>=0;i--){
                       x.waiting(x,tst,event,i,element,token)
                        }
                    })
                
                
            }catch(e){console.log("loadAllPersonalEvents "+e.toString())}
        })
        return feed
    }

    async setCollection(){
        try{document.getElementById(this.state.flyer).outerHTML=""}catch(e){console.log(e)}
        let fly=""+(parseInt(this.state.flyer)+1)
        let d=document.createElement("div")
        d.setAttribute("id",fly)
        d.setAttribute("class","personalfeed_panel")
        try{document.getElementById("personal_feed_container").append(d)}catch(e){console.log(e)}
   
        let tfly=await document.getElementById(fly)
        let count=await this.loadMintedCollections("default",utils.getUrlVars()["account"],this.state.vault,this.state.standard,tfly)
        await this.setState({counter:count.length,loading:"0",flyer:fly})
    }
    
    async loadMintedCollections(blck,who,vault,standard,element){
        let feed="y"
        let x=this
        await window.ethereum.enable().then(async function (accounts) { 
            try{
                //let mevents = new web3.eth.Contract(JSON.parse(REACT_APP_EMITTERABI),library.getVault(vault,standard))
                let mevents = new web3.eth.Contract(utils.abi("erc1155"),document.getElementById("inventory_view").value)
                let block=(blck=="default")?parseInt(await utils.getBlock())-100000:parseInt(blck)
                let eventz 
                
                    let arr=[who]
                    arr=who
                    await mevents.getPastEvents('TransferSingle',
                    {filter : { "to":who} , fromBlock: block, toBlock: 'latest'
                    } , async function(error, event){ 
                        feed=event
                        console.log("loadMintedCards")
                        console.log(event)
    
                        for(let i=event.length-1;i>=0;i--){
                       x.waiting(x,standard,event,i,element)
                        }
                    })
                
                
            }catch(e){console.log("loadMintedCards "+e.toString())}
        })
        return feed
    }



    async loadMintedCollectionsMatic (who,element) {
        let tot=0
        let y=1
        let waiting=[]
      
        let list=await utils.listTracked(who)

        console.log("TRACKED:",list)
        tot=list.length
        this.setState({loading:0,found:tot})
        for(let i=0;i<parseInt(tot);i++){
        try{
            let met=await utils.findMeta(list[i][0],list[i][1])
           
            this.drawNFT(met[1],i,list[i][0],list[i][1])
            }catch(e){console.log(e)}
        }
     
        return tot
    }
    
    
    async drawNFT(ipfs,i,address,id) {
        console.log("IPFS ADDRESS: ",ipfs)
        try{
        let meta=await  utils.itemMetaJSON(utils.uri2url(ipfs))
        console.log("METADATA:",meta)
        let labe=meta["name"]
        let ima=meta["image"]
        
        let dark=meta["foreground_color"]
        let text=meta["color"]
       let tempDiv=document.createElement("div")
       tempDiv.setAttribute("id","submission_"+i)
       tempDiv.setAttribute("class","submission_container")
       let tempImg=document.createElement("img")
       tempImg.onclick=() => window.open("/cyphervault/?cx="+address+"&id="+id)
       tempImg.setAttribute("id","submission_img_"+i)
       tempImg.setAttribute("class","submission_image")
       tempImg.setAttribute("src",utils.uri2url(ima))
       tempDiv.append(tempImg)
       document.getElementById("inventory_feed_container").append(tempDiv)
      
       this.setState({loading:this.state.loading+1})
    
    }catch(e){}
    }

    async waiting(x,standard,event,i,element,token){
        if(x.state.vault!="card")await utils.loadAndDisplayAsync(standard,event,i,element)
        if(x.state.vault=="card")await utils.loadAndDisplayItemFromCollectionAsync(standard,"card",event,i,element)
        let t=parseInt(x.state.loading)+1
        x.setState({loading:t})
    }

    render(){
      
        console.log(this.props) 
        return ( 
            <>
            <div id="Inventory_header">
                            <div id="personal_mainlabel"></div>
                            <select id="personal_view" onChange={() => this.setVault()}>
                            {(0==1)?
                            <>
                            <option value="visual"> VISUAL</option>
                            <option value="vox"> VOX</option>
                            <option value="audio"> AUDIO</option>
                            <option value="video"> VIDEO</option>
                            <option value="lips"> LIPS</option>
                            </>
                            :<></>}
                            <option value="card" selected> CARD</option>
                            </select>
                                
                                {(parseInt(this.state.loading)<parseInt(this.state.counter))?<div id="personal_loadingcounter" className=" text white">loading {parseInt(this.state.loading)} of </div>:<></>}
        
                                <div id="inventory_maincounter" className="dark darkblue text white">{this.state.counter}</div>
                            
                        </div>
                        <div className="clear"/>
                        {(this.state.vault=="cardxxx")?
                        <div id="inventory_select">
                                    <select id="inventory_view" onChange={() => this.setCollection()}>
                                    <option value="0x89b6AC1B89Fade31100588f50903a3659F307a61">CYPHERCARDS</option>
                                    <option value="0xad2644F1DC876AF4259d7c7d503b764B26A91c3b">CYPHERPUNK</option>
                                    <option value="0x89b6AC1B89Fade31100588f50903a3659F307a61">TAROTS</option>
                                    <option value="0xad2644F1DC876AF4259d7c7d503b764B26A91c3b">THE PLOT</option>
                                    </select>
                                </div>
                                :<></>}
        <div id="inventory_feed_container" className="inventory_feed_container text white">
         <MiniMetaList links={this.state.owned}></MiniMetaList> 
        </div>
        </>
        );
    }


}
function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
export default Inventory