                                {(has_erc1155)?<div id="erc1155_standard_filter" className={(this.state.standard=="erc1155")?"personal_setstandard_button dark darkblue text white":"personal_setstandard_button text white"} onClick={() => this.setStandard("erc1155")}>ERC1155</div>:<></>}

                                {(parseInt(this.state.loading)<parseInt(this.state.counter))?<div id="personal_loadingcounter" className=" text white">loading {parseInt(this.state.loading)} of </div>:<></>}
        
                                <div id="personal_maincounter" className="dark darkblue text white">{this.state.counter}</div>
